.sp_only {
	display: none;
}
.pc_only {
	display: block;
}


@include media(max-width 640px) {
	.sp_only {
		display: block;
	}
	.pc_only {
		display: none;
	}

	.inner {
		width: 100%;
	}
	
	#bk_curtains {
		margin-top: -4px;
		font-size: 12px;
		line-height: 2;
	}
	
	.bk_section {
		position: relative;
		padding: 44px;
		box-shadow: none;
	}
	.bk_sec_bg02 {
		background: #ccc;
	}
	
	.bk_spec_sp {
		text-align: center;
		padding: 10px 0 5px;
		.bk_spec_txt {
			display: inline;
			padding: 0 5px 0;
			font-weight: bold;
			a {
				padding-bottom: 5px;
				border-bottom: 3px solid $border-color01;
				&:hover {
					text-decoration: none;
				}
			}
		}
	}
	
	.btn_more {
		text-align: center;
		padding: 28px 0 10px;
		a {
			background: #4d4d4d;
			padding: 10px;
			width: 120px;
			display: inline-block;
			span {
				@include retina-sprite($cccm-sp-btn04_group);
				@include txt-replacement();
				margin: 0 auto;
			}
		}
	}

	#bk_header {
		top: 0px;
		background: #fff;
		@include transition(background 1s ease);
		&.bg_gray {
			background: #999;
		}
		.sp_menu_title {
			text-align: center;
			position: absolute;
			top: 50%;
			left: 50%;
			margin-top: -1 * $cccm_sp_menu_title-height / 2;
			margin-left: -1 * $cccm_sp_menu_title-width / 2;
			&.bk_hidden {
				display: none;
			}
			a {
				@include retina-sprite($cccm_sp_menu_title_group);
				@include txt-replacement();
			}
		}
		.bk_sp_nav_off,
		.bk_sp_nav_on {
			margin: 10px 15px;
		}
		.bk_sp_nav_off {
				@include retina-sprite($cccm_sp_btn01_group);
				@include txt-replacement();
		}
		.bk_sp_nav_on {
				@include retina-sprite($cccm_sp_btn02_group);
				@include txt-replacement();
				display: none;
				opacity: 0;
				@include transition(opacity 1s ease);
		}
		.inner {
			display: none;
			opacity: 0;
			padding: 0;
			position: relative;
			@include transition(opacity 1s ease);
			li {
				display: block;
				&.sp_only {
					display: block !important;
				}
				&.pc_only {
					display: none !important;
				}
				a {
					display: block;
					width: 100%;
					padding: 15px 0;
					text-align: center;
					border-top: 1px solid #fff;
					&.sp_only {
						display: block !important;
					}
					&.pc_only {
						display: none !important;
					}
				}
				&.bk_btn01 {
					span {
						@include retina-sprite($cccm_sp_menu01_group);
						@include txt-replacement();
						top: 0;
					}
				}
				&.bk_btn02 {
					margin-left: 0;
					span {
						@include retina-sprite($cccm_sp_menu02_group);
						@include txt-replacement();
					}
				}
				&.bk_btn03 {
					margin-left: 0;
					padding-left: 0;
					border-left: none;
					span {
						@include retina-sprite($cccm_sp_menu03_group);
						@include txt-replacement();
					}
				}
				&.bk_btn04 {
					margin-left: 0;
					padding-left: 0;
					border-left: none;
					span {
						@include retina-sprite($cccm_sp_menu04_group);
						@include txt-replacement();
					}
				}
				&.bk_btn05 {
					margin-left: 0;
					padding-left: 0;
					border-left: none;
					border-bottom: 1px solid $border-color01;
					span {
						@include retina-sprite($cccm_sp_menu05_group);
						@include txt-replacement();
					}
				}
				&.bk_btn06 {
					margin-left: 0;
					padding-left: 0;
					border-left: none;
					background: #fff;
					span {
						@include retina-sprite($cccm_sp_menu_logo_group);
						@include txt-replacement();
					}
				}
				&.bk_btn_sns01,
				&.bk_btn_sns02,
				&.bk_btn_sns03 {
					margin: 15px 15px 15px 0;
					display: inline;
					position: absolute;
					top: -53px;
					right: 0;
					a {
						padding: 0;
						border-top: none;					
					}
				}
				&.bk_btn_sns01 {
					right: 70px;
				}
				&.bk_btn_sns02 {
					right: 35px;
				}
			}
		}
		.bk_toggled {
			display: block;
			opacity: 1;
			top:0px;
		}
	}

	#bk_footer_sp {
		background: #999;
		color: #fff;
		text-align: center;
		padding: 0px 0px 20px;
		margin-top: 20px;
		.bk_footer_nav_sp {
			width: 100%;
			background: #999;
			li {
				display: block;
				width: 50%;
				border-top: 1px solid #fff;
				text-align: center;
				padding: 0;
				float: left;
				a {
					span {
						margin: 0 auto;
					}
				}
				&.bk_btn_01 {
					width: 100%;
					a {
						display: block;
						padding: 10px 0;
						span {
							@include retina-sprite($cccm_sp_ft_menu01_group);
							@include txt-replacement();
						}
					}
				}
				&.bk_btn_02 {
					a {
						display: block;
						padding: 10px 0;
						span {
							@include retina-sprite($cccm_sp_ft_menu02_group);
							@include txt-replacement();
						}
					}
				}
				&.bk_btn_03 {
					a {
						display: block;
						padding: 10px 0;
						span {
							@include retina-sprite($cccm_sp_ft_menu03_group);
							@include txt-replacement();
						}
					}
				}
				&.bk_btn_04 {
					a {
						display: block;
						padding: 10px 0;
						span {
							@include retina-sprite($cccm_sp_ft_menu04_group);
							@include txt-replacement();
						}
					}
				}
				&.bk_btn_05 {
					a {
						display: block;
						padding: 10px 0;
						span {
							@include retina-sprite($cccm_sp_ft_menu05_group);
							@include txt-replacement();
						}
					}
				}
				&.bk_col_r {
					border-left: 1px solid #fff;
				}
			}
		}
		.bk_btn_totop_sp {
			border-top: 1px solid #fff;
			padding-top: 20px;
			a {
				@include retina-sprite($cccm_sp_btn_back_group);
				@include txt-replacement();
				margin: 0 auto;
			}
		}
		.bk_copyright {
			text-align: center;
			font-size: 10px;
			padding-top: 30px;
		}

	}
	
	&.bk_about_sp {
		#sec01 {
			height: 230px;
			position: relative;
			.bk_txt01 {
				@include retina-sprite($cccm_sp_copy01_group);
				@include txt-replacement();
				position: absolute;
				top: 50%;
				left: 20px;
				margin-top: -1 * $cccm_sp_copy01-height / 2;
				margin-left: 0;
				
			}
		}
		.bk_desc_wrapper {
			background: #ccc !important;
			padding-top: 0;
			.bk_desc_txt {
				padding: 20px;
			}
		}
		#bk_footer_sp {
			background: #fff;
			color: #4d4d4d;
			margin-top: 0;
			.bk_btn_totop_sp {
				a {
					@include retina-sprite($cccm_sp_btn_back_bk_group);
					@include txt-replacement();
					margin: 0 auto;
				}
			}
		}
	}

	#bk_curtains {		
		#sec01 {
			padding-top: 100%;
			background-image:url(../goods/images/cccm_top_photo03.jpg);
			.bk_txt01 {
				@include retina-sprite($cccm-sp-top-title01_group);
				@include txt-replacement();
				margin-top: -1 * $cccm-sp-top-title01-height / 2;
				margin-left: -1 * $cccm-sp-top-title01-width / 2;
			}
		}
	}
	#home_sec02 {
		padding: 22px;
	}
	#home_sec03 {
		padding: 0;
		.bk_sp_visual {
			padding-top: 110%;
			background: url(../goods/images/cccm_lk_img22.jpg) no-repeat 50% 0;
			background-size: cover;
			position: relative;
			.bk_txt01 {
				@include retina-sprite($cccm_sp_title01_group);
				@include txt-replacement();
				position: absolute;
				top: 50%;
				left: 50%;
				margin-left: -1 * $cccm_sp_title01-width / 2;
			}
		}
		.bk_intro {
			padding: 20px;
			.bk_desc_title_01 {
				@include retina-sprite($cccm_sp_subtitle01_group);
				@include txt-replacement();
				margin: 0 auto;
			}
			.bk_desc_txt {
				padding-top: 10px;
			}
		}
	}

	#home_sec04 {
		padding: 0;
		.bk_sp_visual {
			padding-top: 110%;
			background: url(../goods/images/cccm_sb_photo04-2x.jpg) no-repeat 50% 0;
			background-size: cover;
