		}
	}

	#home_sec06 {
		padding: 0;
		.bk_sp_visual {
			padding-top: 110%;
			background: url(../goods/images/cccm_av_photo03.jpg) no-repeat 50% 50%;
			background-size: cover;
			position: relative;
			.bk_txt01 {
				@include retina-sprite($cccm_sp_title04_group);
