	// 商品ページ
	
	.bk_brand {
		padding: 0;
		margin-top: 0;
	}	



	.bk_spec_wrapper {
		text-align: center;
		padding-top: 20px;
		width: 100%;
		margin: 0 auto;
		position: relative;
		overflow: hidden;
		// cursor: pointer;
		&.bk_spec_wrapper_l {
			width: 100%;
		}
		&.bk_spec_wrapper_m {
			width: 50%;
			&.col_l {
				padding-right: 10px;
			}
			&.col_r {
				padding-left: 10px;
			}
		}
		&.bk_spec_wrapper_s {
			width: 33.3%;
			&.col_l {
				padding-right: 13.5px;
			}
			&.col_r {
				padding-left: 13px;
			}
			&.col_c {
				padding: 0 7px 0 6px;
			}
			.bk_spec {
				padding: 0 20px;
			}
		}
		img {
			max-width: 100%;
			height: auto;
		}
	}
	.bk_desc_wrapper {
		padding-top: 20px;
		.bk_desc_txt {
			font-size: 12px;
			line-height: 2;
			padding: 10px;
			margin-top: 0;
			border-top: none;
			border-bottom: none;
			p {
				text-indent: 1em;
			}
		}
	}

	.bk_info_wrapper {
		margin-top: 20px;
		h4 {
			font-size: 15px;
		}
		.bk_info_txt {
			font-size: 12px;
			line-height: 2;
		}
		.bk_info_link {
			font-size: 15px;
			text-align: center;
		}
	}
	.bk_spec {
		display: none !important;
	}

	#sec03 {
		.bk_txt01 {
			@include retina-sprite($cccm_sp_lk_title01_group);
			@include txt-replacement();
			text-align: center;
			margin: 0 auto;
		}
		.bk_desc_title_01 {
			@include retina-sprite($cccm_sp_subtitle01_group);
			@include txt-replacement();
			margin: 0 auto;
		}
		
		.bk_img02 {
			padding-top: 20px;
		}
		.bk_img03,
		.bk_img04 {
			padding-top: 0px;
		}
		.bk_img05 {
			padding-top: 20px;
		}
		.col_c {
			margin: 0px;
			padding-top: 20px;
		}
	}

	#sec04 {
		.bk_txt01 {
			@include retina-sprite($cccm_sp_sb_title01_group);
			@include txt-replacement();
			text-align: center;
			margin: 0 auto;
		}
		.bk_desc_title_01 {
			@include retina-sprite($cccm_sp_subtitle02_group);
			@include txt-replacement();
