		}
		.bk_img04 {
			padding-top: 10px;
			.bk_spec_wrapper {
				padding-top: 0;
			}
		}
	}

	#sec05 {
		.bk_txt01 {
			@include retina-sprite($cccm_sp_rt_title01_group);
			@include txt-replacement();
			text-align: center;
			margin: 0 auto;
		}
		.bk_desc_title_01 {
			@include retina-sprite($cccm_sp_subtitle03_group);
			@include txt-replacement();
			margin: 0 auto;
		}
		.bk_desc_txt {
			padding-bottom: 0;
		}
		.bk_img02,
		.bk_img03 {
			padding-top: 0;
			.bk_spec_wrapper {
				padding-top: 20px;
			}
		}
	}

	#sec06 {
		padding-bottom: 120px;
		&.bk_single_sp {
			padding-bottom: 0;		
		}
		.bk_txt01 {
			@include retina-sprite($cccm_sp_av_title01_group);
			@include txt-replacement();
			text-align: center;
			margin: 0 auto;
		}
		.bk_desc_title_01 {
			@include retina-sprite($cccm_sp_subtitle04_group);
