/**
 * mixins
 */
 
// Original Source: https://gist.github.com/twe4ked
// Modified for assets pipelining on Ruby on Rails
// Added more options to pass

@mixin background-image-retina($file, $type, $width, $height, $positionX: left, $positionY: top, $background-repeat: no-repeat) {
	background-image: url($file + '.' + $type);
	background-size: $width $height;
	width: $width; height: $height;
	background-position: $positionX $positionY;
	background-repeat: $background-repeat;
 
	@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 144dpi) {
		& {
			background-image: url($file + '-2x.' + $type);
			background-size: $width $height;
			width: $width; height: $height;
		}
	}
}

@mixin txt-replacement() {
	display: block;
	text-indent: -9999px;
	overflow: hidden;
}

/* Reset CSS
 * --------------------------------------- */
body {
	background: #fff;
}

.bk_wrapper {
	h1,h2,h3,h4,h5,h6, ul, li, p {
		font-weight: normal;
			padding: 0;
			margin: 0;
	}
}

/*!
	doormat - http://jh3y.github.io/doormat
	@license MIT

	jh3y (c) 2015
!*/
$noOfPanels: 6;
.doormat {
	display : block;
	position: relative;
	margin	: 0;
	padding : 0;
	&__panel {
		visibility: hidden;
		position	: fixed;
		top				: 0;
		right			: 0;
		left			: 0;
		@for $i from 1 through $noOfPanels {
			&:nth-of-type(#{$i}) {
				z-index: 999 - $i;
			}
		}
		&.is--current,
		&.is--current + * {
			visibility: visible;
		}
	}
}


/**
 * cococimo style adjustment
 */
 
#header {
	position: relative;
}

#contents {
	width: 100%;
}
#pan-navi {
	width: 960px;
	margin: 0 auto;
}

@include media(max-width 640px) {
	#pan-navi {
		position: relative;
		top: -20px;	
	}
}

@include media(min-width 641px) {
	#header {
		position: fixed !important;
		overflow: hidden;
		@include transition(height .65s ease);
		&.bk_active {
			height: 42px;
		}
	}
	#footer02 {
		padding-bottom: 1.5em;
		.add_inner {
				width: 15em;
				display: block;
		}
		li {
			display: list-item;
		}
	}
}

